  line-height: normal;
  transition: all 0.1s ease;
}
@media (hover: hover) {
  .btn-secondary:hover {
    background-color: #7875FE;
    color: #fff;
  }
}

.btn-arrow {
  display: flex;
  gap: 5px;
  align-items: center;
  color: #7875FE;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  transition: all 0.3 ease;
}
.btn-arrow__arrow {
  fill: currentColor;
  transition: transform 0.3s ease;
}
@media (hover: hover) {
