.burger-line {
  position: relative;
  width: 20px;
  height: 2px;
  background: #4C5866;
  transition: transform 0.3s ease;
  border-radius: 12px;
}
.burger-line::after, .burger-line::before {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background: #4C5866;
  transition: transform 0.3s ease;
  border-radius: 12px;
}
.burger-line::after {
  bottom: -8px;
}
.burger-line::before {
  top: -8px;
}

.burger-menu--active .burger-line {
  background-color: transparent;
}

.burger-menu--active .burger-line::before {
  transform: rotate(45deg);
  top: 0;
}

.burger-menu--active .burger-line::after {
  transform: rotate(-45deg);
  bottom: 0;
}

